<template>
  <div class="center">
       <div class="center_breadcrumb">
        <crumb></crumb>
    </div>
    <div class="center_content">
         <div class="table-container research">
            <div class="research_box">
                <div @click="addPaper" class="research_add ">
                    <div class="res_b">
                        <img src="@/assets/img/tianjiatimu.png"/>
                        <label>创建空白问卷</label>
                    </div>
                    <span @click="addPaper"  class="new--add">新建</span>
                </div>
                <div @click="addPaperTemplate" class="research_add">
                    <div class="res_b">
                         <img src="@/assets/img/wenjuanmuban.png"/>
                        <label>选择问卷模板</label>
                    </div>
                    <span @click="addPaperTemplate" class="new--add">选择</span>
                </div>
            </div>
            <div class="top-div" v-if="classifyOptions.length !==0">
             <ul class="top-ul">
              <li
                :class="{topActive:topActiveIndex === index}"
                @click="handleClick(vo, index)"
                v-for="(vo,index) in classifyOptions"
                :key="index"
              >{{vo.classifyName}}</li>
            </ul>
          </div>
            <el-table :data="list"  stripe style="width: 100%">
                <el-table-column label="问卷名称"  :show-overflow-tooltip="true">
                    <template slot-scope="scope">
                        {{ scope.row.formName}}
                    </template>
                </el-table-column>
                <el-table-column label="引用次数" >
                    <template slot-scope="scope">
                        <span>{{ scope.row.refCount }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="160">
                    <template slot-scope="scope">
                        <el-button type="text" size="small" @click="openPreview(scope.row.id)">
                            <svgIcon icon-class="iconyulan"></svgIcon>&nbsp;&nbsp;预览
                        </el-button>
                            <el-button type="text" size="small" @click="openQuote(scope.row)">
                            <svgIcon icon-class="iconyinyong"></svgIcon>&nbsp;&nbsp;引用
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
    </div>
 </div>
</template>

<script>
export default {
 name: 'Research',
 components: {},
 data() {
 return {
   pageTitle:'发起调查',
   topActiveIndex: 0,
   classifyOptions:[],
   list:[],
   page:{
      classifyId:'',
      keyword:''  
    },
    api:{
        classify:'/form/rest/FormClassifyBusiness/listAll/v1',
        templateList:'/form/rest/FormTemplateMainBaseBusiness/listAll/v1'
    }
 }
 },
 created(){
     this.classifyAll()
 },
 mounted(){},
 methods:{
      classifyAll(){
       this.$post(this.api.classify).then(res=>{
           if(res.code ===200){
                this.classifyOptions=res.data || []
                if(res.data.length !==0){
                     this.page.classifyId=res.data[0].id
                     this.initData()
                }
               
           }
       })
     },
     initData(){
         this.$post(this.api.templateList, this.page).then(res =>{
             if(res.code ===200){
                 this.list= res.data
             }
         })
     },
    handleClick(vo, index){
        this.topActiveIndex = index;
        this.page.classifyId= vo.id
        this.initData()
    },
    addPaper(){
        this.$router.push({
            path:'/evaluationAndFeedback/addQuestionnaire',
            query:{
                entryType:'new'
            }
        })
    },
    addPaperTemplate(){
        this.$router.push({
            path:'/evaluationAndFeedback/questionnaireLibrary'
        })
    },
     openPreview(id){
         this.$router.push({
             path:'/evaluationAndFeedback/questionnairePreview',
             query:{
                 id:id,
                 entryType:'template'
             }
         })
     },
     openQuote(vo){
        this.$router.push({
             path:'/evaluationAndFeedback/addQuestionnaire',
             query:{
                 id:vo.id,
                 classifyId:vo.classifyId,
                 title:vo.formName,
                 entryType:'copyTemplate',
             }
         })
     }
 }
}
</script>

<style lang='scss'>
 @import '../../styles/center.scss';
 .research{
     width:100%;
  .research_box{
      height:280px;
      background:url('../../assets/img/bg-chaxun.png') no-repeat center center;
      background-size:100% 100%;
      border-radius:10px 10px 0px 0px;
      display:flex;
      justify-content: center;
      align-items: center;
      .research_add{
         height:100%;
         width:280px;
         display:flex;
         flex-direction:column;
         justify-content: center;
         align-items: center;
         position:relative;
         overflow:hidden;
         .res_b{
            width:280px;
            height:160px;
            background:rgba(255,255,255,1);
            border-radius:10px;
            margin-bottom:20px;
            display:flex;
            flex-direction:column;
            justify-content: center;
            align-items: center;
            img{
                width:60px;
                height:65px;
                margin-bottom:20px;
            }
            label{
                font-size:14px;
                font-weight:bold;
                color:#03B187;
                line-height:14px;
            }
         }
         .new--add{
            width:100px;
            height:40px;
            background:rgba(255,255,255,1);
            border-radius:20px;
            font-size:14px;
            font-weight:bold;
            color:#03B187;
            text-align:center;
            line-height:40px;
            cursor:pointer;
         }
      }
      .research_add:nth-of-type(1){
          margin-right:30.4vw;
      }
  }
  .top-div{
        min-height:40px;
        width:100%;
        background: #f8f8f8;
        padding: 20px 0;
        .top-ul{
            li{
                padding:10px 14px;              
                cursor:pointer;
                float:left;
                color:#696969;
                margin-bottom:10px;
                margin-right: 20px;
                font-size:14px;
                border-radius:20px;
                background: #f8f8f8;
                &.topActive{
                    color:#fff;
                    font-weight:bold;
                    background:rgba(3,177,135,1);
                    box-shadow:0px 2px 20px 0px rgba(3,177,135,0.3);
                }
            }
        
        }
    } 
 }
</style>



